.header {
   background-color: $primaryColor;

   @media screen and (min-width: 768px) {
      box-shadow: 0 4px 5px rgba(0, 0, 0, 0.15);
   }

   .container {
      @extend %container;
      margin: 0 auto;
   }

   &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 60px;

      @media screen and (min-width: 768px) {
         height: 100px;
      }
   }

   &__wrap {
      display: flex;
      align-items: center;
   }

   &__logo {
      margin-right: 45px;

      @media screen and (min-width: 576px) {
         margin-right: 23px;
      }

      @media screen and (min-width: 768px) {
         height: 36px;
      }
   }


   &__city-link {
      @include text(14px, 400, 17px, $linkColor);

      @media screen and (min-width: 576px) {
         display: none;
      }
   }

   &__phone {
      display: none;

      span {
         @include text(14px, 400, 17px, $textColorLight);
      }

      @media screen and (min-width: 576px) {
         display: block;
      }
   }

   &__phone__link {
      @include text(14px, 400, 17px, $textColor);
   }

   &__user {
      @media screen and (min-width: 1280px) {
         display: none;
      }
   }

}